}
.ui-datepicker-calendar .ui-state-active {
  background: #fff2aa;
  border: 1px solid #c19163;
  color: #666;
  -webkit-box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.1);
